@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
body {
  min-width: 800px;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
background: rgb(6,74,18);
background: linear-gradient(95deg, rgba(6,74,18,1) 0%, rgba(63,134,89,1) 49%, rgba(119,217,203,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#064a12",endColorstr="#77d9cb",GradientType=1);
  background-attachment: fixed;
  font-family: 'Lora', Courier, Monospace;
}

button {
	cursor: pointer;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*.App {
  text-align: center;
}*/

.center {
  display:flex;
  justify-content: center;
}

.spread {
  display: flex;
  justify-content: flex-end;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
}

.header {
  margin-top: -200px;
  width: 100%;
  height:70vh;
  position: absolute;
  background: url(/find-the-face/static/media/bckg2.0952788a.jpg);
  background-size: cover;
  background-position: center;
  z-index: -1;
  transform: skewY(-10deg);
  box-shadow: 15px 15px 15px 10px #212121;
}

.cont-grid{
/*  width: 800px;*/
  margin-top: 10vh;
  min-width: 800px;
  max-width: 80vw;
  display: grid;
  background: linear-gradient(180deg, rgba(6,74,18,1) 0%, rgba(63,134,89,1) 49%, rgba(119,217,203,1) 100%);
  box-shadow: 10px 10px 10px 10px black;
  min-height: 600px;
  grid-template-columns: 5% 20% 20% 55%;
  grid-template-rows: 25% 25% 40% auto;
}

.item-a {
  display: grid;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
}

.item-b {
  font-size: 70px;
  font-family: sans-serif;
  font-weight: 800;
  display: grid;
  grid-column-start: 3;  
  grid-row-start: 1;
  grid-row-end: 3;
  background-color: white;
}

.item-c {
  grid-column-start: 2;
  grid-row-start: 2;
}

.item-d {
  grid-column-start: 2;
  grid-column-end: 4;
  grid-row-start: 3;
  margin-top: 100px;
}

.item-e {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 5;
}

.nav {
	display:flex;
	justify-content: flex-end;
	margin-right: 5%;
}

.nav-box {
	background: rgb(60,110,67);
background: linear-gradient(330deg, rgba(60,110,67,1) 0%, rgba(197,232,210,1) 49%, rgba(179,233,180,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#3c6e43",endColorstr="#b3e9b4",GradientType=1);
}
.skew-back {
  transform: skewY(10deg);
}

.head-img {
	margin-left: 5%;
	margin-top: 15vh;
}

.hd-margin {
	margin-left: 5%;
	margin-top: 50px;
}

h2 {
	font-size: 1.2rem;
	background: -webkit-linear-gradient(45deg, #064a12, #57d9cb);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.signin {
  margin-top: 300px;
  background-color: white;
}
.form {
	width: 700px;
	background-color: #6d695c;
	background-image:
	repeating-linear-gradient(120deg, rgba(255,255,255,.1), rgba(255,255,255,.1) 1px, transparent 1px, transparent 60px),
	repeating-linear-gradient(60deg, rgba(255,255,255,.1), rgba(255,255,255,.1) 1px, transparent 1px, transparent 60px),
	linear-gradient(60deg, rgba(0,0,0,.1) 25%, transparent 25%, transparent 75%, rgba(0,0,0,.1) 75%, rgba(0,0,0,.1)),
	linear-gradient(120deg, rgba(0,0,0,.1) 25%, transparent 25%, transparent 75%, rgba(0,0,0,.1) 75%, rgba(0,0,0,.1));
	background-size: 70px 120px;
}
 .bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

.image {
	display: block;
  max-width:500px;
  max-height:500px;
  width: auto;
  height: auto;
}
